import ArrayUtils from '../utils/array';

let GameOfLife = (function() {
  const DEAD = 0;
  const ALIVE = 1;

  function init(seed) {
    this.board = ArrayUtils.clone2dArray(seed);
  }

  function *next() {
    this.diff = {};
    this.previousBoard = ArrayUtils.clone2dArray(this.board);
    //todo go through and generate next board
    yield {
      board: this.board,
      diff: this.diff
    };
  }

  function toString() {
    return 'test';
  }

  return {
    init: init,
    next: next,
    toString: toString
  };

}());

export default GameOfLife;



